.nav {
  padding: 40px;
  background: #000;
}
.nav .logo {
  color: #fff;
}

.cnt  {
  position: relative;
}
.blog ul li {
  padding: 30px;
  border-bottom: 1px solid #000;
  position: relative;
}

.list {
  margin-top: 50px;
  border-top: 1px solid #000;
}

.btn_delete {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  padding: 5px;
  background: #000;
  color: #fff;
  border: none;
  cursor: pointer;
}

.title {
  cursor: pointer;
  font-weight: bold;
  font-size: 1.2rem;
}

.title a {
  display: inline-block;
  font-weight: normal;
  margin-left: 30px;
  font-size: .8rem;
}

.sort {
  position: absolute;
  right: 30px;
  top: -37px;
  padding: 3px;
  font-size: 11px;
}

.date {
  font-weight: normal;
  font-size: .75rem;
  color: #888;
  margin-top: 10px;
}

.search {
  padding: 30px;
  text-align: center;
}

.search input {
  height: 30px;
  width: 50%;
  padding: 5px;
  box-sizing: border-box;
}

.search button {
  display: inline-block;
  padding:5px;
  
  border: none;
  background: #000;
  color: #fff;
  line-height: 19px;
  margin-left: 10px;
}

.modal {
   border: 1px solid #000;
   width: 70%;
   padding: 5%;
   background: #fff;
   z-index: 1;
   position: fixed;
   top: 50%;
   left: 50%;
   transform: translate(-50%,-50%);
   overflow: hidden;
}

.modal h4 {
  margin-bottom: 20px;
}

.modal p {
  font-size: .78rem;
  margin-bottom: 10px;
}

.modal .modify {
  float: right;
  background: #000;
  border: none;
  color:#fff;
  padding: 5px;
}

.modal .close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #fff;
  border: none;
  font-size: 20px;
}

.modal h4 input {
  height: 30px;
  padding:2px 5px;
  font-size: 1rem;
}